.nav__link {
  display: flex;
  font-weight: 500;
  font-size: 14px;
  line-height: calc(18 / 14);
  border-radius: 36px;
  align-items: flex-end;
  min-width: 72px;
  // min-height: 34px;
  padding: 8px 16px;

  // padding-top: 32px;
  // padding-bottom: 13px;
  transition: background-color 250ms linear, color 250ms linear;

  &:hover,
  &:focus {
    background-color: var(--accent-color-dark-bg);
    font-weight: 700;
    .dark & {
      color: #111111;
    }
  }
  .dark & {
    color: var(--secondary-text-color-dark-bg);
  }
}

.nav__link--current {
  background-color: var(--accent-color-dark-bg);
  font-weight: 700;
  .dark & {
    color: #111111;
  }
}

